Office.onReady(() => {
  console.log("Office.js ready v2");

  CustomFunctions.associate("MX.COMPLIANT", checkCompliant);

  document.getElementById("login-btn").addEventListener("click", login);
  document.getElementById("secure-btn").addEventListener("click", callSecureAPI);

  checkLoginStatus();
});

const apiBase = "https://env-muslimxchange-staging.kinsta.cloud/wp-json/mx/v1";

function saveTokenToIndexedDB(token) {
  const dbReq = indexedDB.open("mxAddinDB", 1);
  dbReq.onupgradeneeded = event => {
      const db = event.target.result;
      db.createObjectStore("tokens");
  };
  dbReq.onsuccess = () => {
      const db = dbReq.result;
      const tx = db.transaction("tokens", "readwrite");
      tx.objectStore("tokens").put(token, "jwt");
  };
}

function getTokenFromIndexedDB() {
  return new Promise((resolve, reject) => {
      const dbReq = indexedDB.open("mxAddinDB", 1);
      dbReq.onsuccess = () => {
          const db = dbReq.result;
          const tx = db.transaction("tokens", "readonly");
          const req = tx.objectStore("tokens").get("jwt");
          req.onsuccess = () => resolve(req.result);
          req.onerror = reject;
      };
  });
}

function clearTokenFromIndexedDB() {
  const dbReq = indexedDB.open("mxAddinDB", 1);
  dbReq.onsuccess = () => {
      const db = dbReq.result;
      const tx = db.transaction("tokens", "readwrite");
      tx.objectStore("tokens").delete("jwt");
  };
}

async function checkLoginStatus() {
  const token = await getTokenFromIndexedDB();
  const loginSection = document.getElementById("login-section");
  const statusDiv = document.getElementById("login-status");
  const logoutBtn = document.getElementById("logout-btn");

  if (token) {
      try {
          const res = await fetch(`${apiBase}/secure-hello`, {
              headers: {
                  Authorization: `Bearer ${token}`
              }
          });

          if (res.ok) {
              const data = await res.json();
              loginSection.style.display = "none";
              statusDiv.innerText = `✅ Logged in as ${data.user.name}`;
              logoutBtn.style.display = "inline-block";
          }
      } catch (err) {
          console.error("Error checking login status", err);
      }
  }
}

async function login() {
  const username = document.getElementById("username").value.trim();
  const password = document.getElementById("password").value;
  const statusDiv = document.getElementById("login-status");
  const loginSection = document.getElementById("login-section");
  const logoutBtn = document.getElementById("logout-btn");

  statusDiv.innerText = "🔄 Logging in...";

  try {
      const res = await fetch(`${apiBase}/login`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ username, password }),
      });

      const json = await res.json();

      if (!res.ok) {
          console.error("Login failed:", json);
          statusDiv.innerText = `❌ ${json.message || "Login failed."}`;
          return;
      }

      const { token, user } = json;

      saveTokenToIndexedDB(token);
      loginSection.style.display = "none";
      statusDiv.innerText = `✅ Logged in as ${user.name}`;
      logoutBtn.style.display = "inline-block";

  } catch (err) {
      console.error("Login error:", err);
      statusDiv.innerText = "❌ Network or CORS error during login.";
  }
}

function logout() {
  clearTokenFromIndexedDB();
  document.getElementById("login-section").style.display = "block";
  document.getElementById("logout-btn").style.display = "none";
  document.getElementById("login-status").innerText = "Logged out.";
}

async function callSecureAPI() {
  const responseDiv = document.getElementById("api-response");
  const token = await getTokenFromIndexedDB();

  if (!token) {
      responseDiv.innerText = "❌ No token found. Please log in.";
      return;
  }

  try {
      const res = await fetch(`${apiBase}/secure-hello`, {
          headers: {
              Authorization: `Bearer ${token}`
          }
      });

      if (!res.ok) {
          responseDiv.innerText = "❌ Secure call failed.";
          return;
      }

      const data = await res.json();
      responseDiv.innerText = `✅ ${data.message} from ${data.user.name}`;
  } catch (err) {
      responseDiv.innerText = "❌ API request error.";
      console.error(err);
  }
}

/**
* Custom function to check if a ticker is compliant using MX API.
* @customfunction
* @param {string} ticker The ticker symbol to check.
* @returns {Promise<string>} The compliance status.
*/
async function checkCompliant(ticker) {
  try {
      const res = await fetch(`${apiBase}/hello?ticker=${encodeURIComponent(ticker)}`);
      if (!res.ok) return "❌ Error";

      const data = await res.json();
      return data.message || `✅ ${ticker} Compliant`;
  } catch (err) {
      console.error("Compliance check failed:", err);
      return "❌ API Error";
  }
}
